.g-btn {
    color: #fff;
    background-color: #fd6400;
    border-color: #fd6400;
}

.mat-form-field {
    width: 100%;
}

.mat-expansion-panel:not([class*="mat-elevation-z"]) {
    box-shadow: none;
}

.mat-expansion-panel {
    background: transparent;
    color: rgba(0, 0, 0, 0.87);
}

.patient-table {
    width: 99%;
}

.mat-card {
    height: 100%;
}

.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):active,
.show>.btn-secondary.dropdown-toggle {
    color: #fff;
    background-color: #fd6400;
    border-color: #fd6400;
}

.btn-secondary {
