    font-weight: 400;
}

.example-container {
    /* height: 67vh; */
    height: 400px;
    padding: 12px;
    overflow: auto;
}

table {
    width: 100%;
}

.select-outer {
    float: right;
}

.heading {
    margin: 0;
    font-size: 22px;
    font-weight: 400;
}

.color1 {
    background: #0eab1c;
    height: 28px !important;
    display: inline-block;
    align-items: flex-end;
    vertical-align: middle;
    width: 5px;
    margin-right: 17px;
}

.completed {
    background: #0eab1c;
    height: 28px !important;
    display: inline-block;
    align-items: flex-end;
    vertical-align: middle;
    width: 5px;
    margin-right: 17px;
}

.pending {
    background: #969696;
    height: 28px !important;
    display: inline-block;
    align-items: flex-end;
    vertical-align: middle;
    width: 5px;
    margin-right: 17px;
}

.sort {
    width: 170px;
    float: right;
    font-family: "GothamNarrowBold", sans-serif;
}

.sort-text {
