}

.any-src {
    padding-right: 16px;
    /* min-width: 93px; */
    text-align: left;
    white-space: nowrap;
}

.synch {
    font-size: 12px;
    margin-right: 15px;
}

.all {
    margin-left: 13px;
    font-weight: bold;
    font-size: 14px;
    font-family: "GothamNarrowBold", sans-serif;
}

.head1 {
    text-align: left !important;
    font-size: 17px;
    /* text-transform: capitalize; */
}

.patient-name {
    font-weight: 400;
    color: #0075bc;
    font-family: "GothamNarrowBold", sans-serif;
}

.example-container table tr th:nth-of-type(2),
.example-container table tr td:nth-of-type(2).example-container table tr th:nth-of-type(3),
.example-container table tr td:nth-of-type(3) {
    min-width: 98px !important;
}

.imgouter {
    width: 42px;
    text-align: center;
}

.norecord {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 10px;
    border: 1px solid #ccc9;
    border-top: transparent;
    background-color: #fbfbfb;
}

td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type,
th.mat-header-cell:last-of-type {
    padding-right: 12px;
}

.example-container tr td:last-child,
.example-container tr th:last-child {
    max-width: 150px;
    width: 100px;
    white-space: nowrap;
    text-align: left;
}

table tr th:first-child {
    border-radius: 5px 0 0 0px !important;
}

table tr th:last-child {
    border-radius: 0px 5px 0 0px !important;
}

.example-container1 {
    /*height: calc(100vh - 90px);*/
    height: calc(100vh - 20px);
    /* margin-top: 70px; */
    position: relative;
    z-index: 0;
}

.example-sidenav {
    padding: 20px;
}

.dashboard-wrapper {
    padding-left: 0 !important;
}

.ipadfiler {
    display: none;
}

.example-sidenav {
    padding: 0;
}

.dashboard-wrapper {
    padding-left: 0 !important;
}

@media (max-width: 1025px) {
    .dashboard-wrapper {
        padding-left: 12px !important;
        padding-right: 12px !important;
    }

    .btn-secondary {
        min-width: auto;
    }

    .toprow {
        flex-direction: row;
        padding-right: 0 !important;
    }

    .ipadfiler {
        display: block;
    }

    .example-sidenav {
        padding: 0;
    }

    .mat-card {
        border-radius: 10px !important;
    }

    .ipadfiler button {
        /* min-width: 48px; */
        min-width: 75px;
        padding: 0;
        margin-left: 0;
    }

