        padding-left: 8%;
    }
}

.plist-date {
    font-size: 11px;
    /* width: 77px; */
    word-break: break-all;
    white-space: normal;
    text-align: left;
    display: block;
}

td.mat-cell:first-of-type,
td.mat-footer-cell:first-of-type,
th.mat-header-cell:first-of-type {

    min-width: 106px !important;
    word-break: break-all !important;
}

.mat-row:hover {
    background-color: whitesmoke !important;
}

td.mat-cell {
    cursor: default !important;
}

i.filtertxt {
    color: #ED8B00 !important;
    font-style: normal;
    font-size: 16px;
    margin-left: 5px;

}

@media screen and (min-width:768px) {
    th.mat-header-cell {
        white-space: nowrap !important;
        padding-right: 23px !important;
    }
}

.m-card h5 {
    display: none;
}

/*responsive css start*/
@media screen and (max-width: 767px) {

    table,
    thead,
    tbody,
    th,
    td,
    tr {
        display: inline-block;
    }

    .radio-custom+.radio-custom-label:before {
        line-height: 1.5;
    }

    .number-tab {
        margin-bottom: 15px;
    }

    ::ng-deep .mat-dialog-container {
        background: #f0eff4;
    }

    table tr td:first-child {
        position: absolute;
        right: -120px;
        top: -3px;
        z-index: 9999;
    }

    ::ng-deep .cdk-overlay-pane {
        min-width: initial !important;
        max-width: 96vw !important;
        width: 100% !important;
    }

    table tr td:nth-of-type(4) {
        max-width: 100% !important;
    }

    ::ng-deep .modal-dialog {
        padding: 0 15px;
    }

    .title-card {
        display: none;
    }

    .page-title {
        padding: 0;
        font-size: 17px;
    }

    .m-card h5 {
        display: block;
        /* padding-left: 15%; */
    }

    table {
        width: 100%;
    }

    thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }

    tr {
        border: none
    }

    td {
        border: none;
        border-bottom: 1px solid #eee;
        position: relative;
        padding-left: 50%;
    }

    td span.textTtl {
        display: inline-block !important;
        position: absolute;
        top: 50%;
        left: 25px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
        transform: translate(0, -50%);
        font-weight: bold;
        color: #000;
    }

    table tr {
        height: 100%;
        margin-bottom: 25px;
        position: relative;
    }

    table tr {
        border: none;
        margin-bottom: 20px;
        border-radius: 10px;
        width: 100%;
        box-shadow: 0 0 12px 0 rgb(0 0 0 / 19%) !important;
    }

    .example-sidenav-content {
        background: #ececef;
    }

    .mat-table {
        background: transparent;
    }

    tr.mat-header-row {
        height: 0 !important;
    }

    table tr th {
        display: none;
    }

    .btn-toggle:after {
        width: 5rem;
    }

    table tr td {
        width: 100% !important;
        padding-left: 50% !important;
        text-align: left !important;
        height: 45px;
        line-height: 12px;
        padding-top: 15px;
        padding-right: 15px;
        border: none;
    }

    table tr td:last-child {
        padding-top: 5px;
    }

    .example-container tr td:last-child {
        max-width: 100%;
    }

    .mat-table,
    .dashboard .mat-card {
        background: transparent;
    }

    .toprow {
        background: #fff;
        box-shadow: 0 0 12px 0 rgb(0 0 0 / 19%) !important;
        padding-bottom: 0.5rem !important;
        margin-bottom: 15px;
        border-bottom: 2px solid #ccc;
        border-top: 2px solid #ccc;
        padding-left: 20% !important;
    }

    .example-container1 {
        margin-top: 0;
    }

    .dashboard-wrapper {
        padding-left: 0px !important;
        padding-right: 0px !important;
    }

    .norecord {
        background-color: #ffffff !important;
        border-radius: 10px !important;
        box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
    }

    .norecord tr td {
        width: 100% !important;
        text-align: center;
        border: none;
    }

    .sidefilter .head1 {
        padding-left: 23%;
    }

    .norecord td::before {
        display: none;
    }

    .norecord td {
        padding-left: 0 !important;
    }

    .example-container {
        height: calc(100vh - 335px);
        padding: 0px 6px 0 6px !important;
        overflow-y: scroll;
        overflow-x: hidden;
    }

    .patient-table td.mat-cell:first-of-type,
    .patient-table td.mat-footer-cell:first-of-type {
        font-family: "gothambook", sans-serif !important;
    }

    .rowpart {
        flex-direction: column-reverse;
    }

    .rowpart .number-tab {
        min-width: 100%;
    }

    .rowpart .md-stepper-horizontal {
        background: transparent;
        padding-bottom: 0 !important;
    }

    .nopad {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .labelpart,
    .txtpart {
        padding-top: 20px;
    }

}

@media screen and (min-width: 768px) and (max-width:1024px) {
    .example-container {
        overflow: scroll;
    }
}

.left-block {
    height: auto !important;
}

::ng-deep .mat-dialog-title {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
}

::ng-deep .mat-toolbar {
    background-color: #1072ce;
    color: #fff;
    text-align: center;
    justify-content: center;
    position: relative;
}

::ng-deep .mat-dialog-container {
    padding: 0 !important;
    overflow: hidden;
}

::ng-deep .mat-dialog-content {
    margin: 0 20px 0 0;
    padding: 0px;
    overflow-x: hidden;
    overflow-y: auto;
}

.md-stepper-horizontal {
    display: table;
    width: 100%;
    margin: 0 auto;
    background-color: #FFFFFF;
}

.md-stepper-horizontal .md-step {
    display: table-cell;
    position: relative;
}

.md-stepper-horizontal .md-step:active {
    border-radius: 15% / 75%;
}

.md-stepper-horizontal .md-step:first-child:active {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.md-stepper-horizontal .md-step:last-child:active {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.md-stepper-horizontal .md-step .md-step-circle:hover {
    cursor: pointer;
}

.md-stepper-horizontal .md-step:first-child .md-step-bar-left,
.md-stepper-horizontal .md-step:last-child .md-step-bar-right {
    display: none;
}

.md-stepper-horizontal .md-step .md-step-circle {
    width: 30px;
    height: 30px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
    font-size: 13px;
    font-weight: 600;
    color: rgb(44, 41, 41);
    border: 1px solid #ccc;
}

.md-stepper-horizontal.green .md-step.active .md-step-circle {
    background-color: #00AE4D;
}

.md-stepper-horizontal.orange .md-step.active .md-step-circle {
    background-color: #1072ce;
    color: #fff;
}

.md-stepper-horizontal .md-step.active .md-step-circle {
    background-color: rgb(33, 150, 243);
}

.md-stepper-horizontal .md-step.done .md-step-circle:before {
    font-family: 'FontAwesome', sans-serif;
    font-weight: 100;
    content: "\f00c";
}

.md-stepper-horizontal .md-step.done .md-step-circle *,
.md-stepper-horizontal .md-step.editable .md-step-circle * {
    display: none;
}

.md-stepper-horizontal .md-step.editable .md-step-circle {
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}

.md-stepper-horizontal .md-step.editable .md-step-circle:before {
    font-family: 'FontAwesome', sans-serif;
    font-weight: 100;
    content: "\f040";
}

.md-stepper-horizontal .md-step .md-step-title {
    margin-top: 16px;
    font-size: 16px;
    font-weight: 600;
}

.md-stepper-horizontal .md-step .md-step-title,
.md-stepper-horizontal .md-step .md-step-optional {
    text-align: center;
    color: rgba(0, 0, 0, .26);
}

.md-stepper-horizontal .md-step.active .md-step-title {
    font-weight: 600;
    color: rgba(0, 0, 0, .87);
}

.md-stepper-horizontal .md-step.active.done .md-step-title,
.md-stepper-horizontal .md-step.active.editable .md-step-title {
    font-weight: 600;
}

.md-stepper-horizontal .md-step .md-step-optional {
    font-size: 12px;
}

.md-stepper-horizontal .md-step.active .md-step-optional {
    color: rgba(0, 0, 0, .54);
}

.md-stepper-horizontal .md-step .md-step-bar-left,
.md-stepper-horizontal .md-step .md-step-bar-right {
    position: absolute;
    top: 14px;
    height: 1px;
    border-top: 1px solid #DDDDDD;
}

.md-stepper-horizontal .md-step .md-step-bar-right {
    right: 0;
    left: 50%;
    margin-left: 20px;
}

.md-stepper-horizontal .md-step .md-step-bar-left {
    left: 0;
    right: 50%;
    margin-right: 20px;
}

button.refbtn {
    background: #f65c00;
    color: #fff;
    border: 1px solid #f65c00;
    padding: 0 15px;
}

::ng-deep .mat-dialog-actions {
    justify-content: center;
    margin-bottom: 30px;
}

::ng-deep .mat-form-field-appearance-outline .mat-form-field-wrapper {
    margin: 0 !important;
}

::ng-deep .example-container {
    margin: 0 !important;
    padding: 0 !important;
}

::ng-deep .mat-form-field-wrapper {
    padding-bottom: 0 !important;
}

.checkbox-custom,
.radio-custom {
    opacity: 0;
    position: absolute;
}

.checkbox-custom+.checkbox-custom-label:before,
.radio-custom+.radio-custom-label:before {
    content: '';
    background: #fff;
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    text-align: center;
}

.checkbox-custom:checked+.checkbox-custom-label:before {
    content: "\f00c";
    font-family: 'FontAwesome', sans-serif;
    background: rebeccapurple;
    color: #fff;
}

.radio-custom+.radio-custom-label:before {
    border-radius: 50%;
}

.radio-custom:checked+.radio-custom-label:before {
    content: "\2713";
    color: #fff;
    background: green;
}

.checkbox-custom:focus+.checkbox-custom-label,
.radio-custom:focus+.radio-custom-label {
    outline: 1px solid #ddd;
    /* focus style */
}

::ng-deep .modal-header.mgp {
    background: none !important;
    border: none !important;
}

::ng-deep .modal-content {
    border: none;
    -webkit-box-shadow: -2px 3px 15px 0px rgb(0 0 0 / 50%);
    -moz-box-shadow: -2px 3px 15px 0px rgba(0, 0, 0, 0.5);
    box-shadow: -2px 3px 15px 0px rgb(0 0 0 / 50%);
}

::ng-deep .confirm-modal {
    max-height: 500px;
    overflow: hidden;
}

::ng-deep .cdk-overlay-pane {
    min-width: 400px;
}

td.mat-cell:first-of-type,
th.mat-header-cell:first-of-type {
    min-width: 0 !important;
    width: 50px !important;
}

::ng-deep .modal {
    z-index: 10010 !important;
}

::ng-deep .modal-backdrop {
    z-index: 10000 !important;
}

p {
    padding: 0;
    margin: 0;
}

.col-form-label_heading{
    font-weight: bolder;
}

.close {
    color: #fff !important;
    position: absolute;
    float: right;
    right: 10px;
    top: 10px;
}